.container {
  width: 1000px;
  height: auto;
  margin: 0 auto;
}

header {
  width: 100%;
  height: auto;
  background-color: #add8e6;
}

form {
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 15px 0;
  display: flex;
}

input {
  width: 500px;
  background-color: rgba(255, 255, 255, .5);
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4d4d8d;
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 10px 15px;
}

button:hover {
  background-color: #090925;
}

.gallery {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-top: 20px;
  display: grid;
}

img {
  max-width: 100%;
  height: auto;
  flex: 1;
  display: block;
}

a {
  color: #000;
  text-align: center;
  border: 1px solid #d3d3d3;
  border-radius: 5px;
  text-decoration: none;
  overflow: hidden;
}

.photo-card {
  height: 100%;
  flex-direction: column;
  display: flex;
}

.info {
  justify-content: center;
  align-items: center;
  display: flex;
}

